import { Row, Col, Space } from 'antd';

const background = {
    background: 'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/img/pictures/etna.jpg")',
    backgroundSize: 'cover',
    backgroundPosition: 'center'
};

function App() {
    return (
        <>
            <div className="slide slide-first white-text" style={background}>
                <div className="centered">
                    <Row>
                        <Col xs={{ offset: 2, span: 20 }} sm={{ offset: 3, span: 18 }} lg={{ offset: 6, span: 12 }}>
                            <img src="/img/horny.png" alt="mascotte" style={{ width: "192px", margin: '20px' }} />
                            <h2>Weird Frames was founded in Catania in 2013 with the goal of creating new stories for people to experience.</h2>
                        </Col>
                    </Row>
                </div>
            </div>
            <div className="jumbotron">
                <div className="jumbotron-content">
                    <h1>Our Mission</h1>
                    <p>At Weird Frames, our mission is to give people stories to live and get lost in.</p>
                </div>
            </div>
            <div className="slide">
                <div className="centered">
                    <Space direction="vertical" size="large">
                        <h1>The Founders</h1>
                        <Row>
                            <Col xs={{ offset: 3, span: 18 }} md={{ offset: 6, span: 12}} lg={{ offset: 5, span: 6 }}>
                                <img src="/img/avatar_gvecchio.png" alt="mascotte" style={{ width: "212px", margin: '20px' }} />
                                <h2>Giuseppe Vecchio</h2>
                                <h3 className="justified">
                                    Computer Engineer, data scientist and 3D artist.
                                    Graduated in <i>Computer Engineering</i> in 2020 at University of Catania. 
                                    Worked for three years as a software developer and engineer and is currently pursuing a Ph.D. in Deep Learning and Artificial Intelligence 
                                    with 3 active publications in the fileds of graphics and robotics. 
                                </h3>
                            </Col>

                            <Col xs={{ offset: 3, span: 18 }} md={{ offset: 6, span: 12}} lg={{ offset: 2, span: 6 }}>
                                <img src="/img/avatar_gtomarchio.png" alt="mascotte" style={{ width: "212px", margin: '20px' }} />
                                <h2>Giuliano Tomarchio</h2>
                                <h3 className="justified">
                                    Screenplay writer, video editor and filmaker. 
                                    Graduated with honors in <i>Performing Arts and the Science of Performance</i> in 2021 at University Sapienza of Rome.
                                    Worked as intern in <i>Mercato Internazionale Audiovisivo</i> and attended a two-year Screenwriting course at the <i>Sentieri Selvaggi Film School</i>.
                                </h3>
                            </Col>
                        </Row>
                    </Space>
                </div>
            </div>
        </>
    );
}

export default App;
